import { createStore } from "vuex";

export default createStore({
  state() {
    return {
      products: [
        {
          title: "test",
          price: 10,
          description: "loret ipsum",
          image: "test",
          category: "test",
          rate: {
            rating: 1,
            count: 1,
          },
        },
      ],
      filters: [],
      cartItems: [],
      sort: {
        type: "rating",
        sign: "-",
      },
    };
  },
  mutations: {
    updateProducts(state, data) {
      state.products = data;
    },
    addFilter(state, filter) {
      state.filters.push(filter);
    },
    deleteFilter(state, filter) {
      state.filters.splice(state.filters.indexOf(filter), 1);
    },
    addItemToCart(state, item) {
      state.cartItems.push(item);
    },
    deleteItemFromCart(state, item) {
      state.cartItems.splice(state.cartItems.indexOf(item), 1);
    },
  },
  getters: {
    getProducts(state) {
      return state.products.sort((a, b) => {
        if (state.sort.sign === "+") {
          const bridge = a;
          a = b;
          b = bridge;
        }
        if (state.sort.type === "rating") {
          return a[state.sort.type]["rate"] - b[state.sort.type]["rate"];
        }
        return a[state.sort.type] - b[state.sort.type];
      });
    },
    getCategories(state) {
      return state.products.map((product) => product.category.toUpperCase());
    },
  },
  actions: {
    async apiCall({ commit }) {
      try {
        fetch("https://fakestoreapi.com/products")
          .then((res) => res.json())
          .then((json) => commit("updateProducts", json));
      } catch (error) {
        console.log("Api error: " + error);
      }
    },
  },
});
